import { Formik } from 'formik'
import * as Yup from 'yup'

const Index = () => (
 <Formik
      initialValues={{ playlistName: '' }}
    >
      {props => {
        const {
          values,
          touched,
          errors,
          dirty,
          isSubmitting,
          handleChange,
          handleBlur,
          handleSubmit,
          handleReset,
        } = props;
        return (
          <form onSubmit={handleSubmit}>
            <label htmlFor="Playlist Name" style={{ display: 'block' }}>
              Playlist Name
            </label>
            <input
              id="playlistName"
              placeholder="Enter your Playlist Name"
              type="text"
              value={values.playlistName}
              onChange={handleChange}
              onBlur={handleBlur}
              className={
                errors.playlistName && touched.playlistName ? 'text-input error' : 'text-input'
              }
            />
            {errors.playlistName && touched.playlistName && (
              <div className="input-feedback">{errors.playlistName}</div>
            )}

            <button
              type="button"
              className="outline"
              onClick={handleReset}
              disabled={!dirty || isSubmitting}
            >
              Reset
            </button>
            <button type="submit" disabled={isSubmitting}>
              Submit
            </button>

          </form>
        );
      }}
    </Formik>
)

export default Index